.header {
    background-color: #5c42aa;
    &__burger {
        display: none;
        height: 54px;
        //    width: 54px;
        span {
            display: block;
            width: 24px;
            height: 3px;
            background-color: #fff;
            &:nth-child(2) {
                margin: 3px 0;
            }
        }
    }
    &__container {
        display: flex;
        // align-items: center;
    }

    &__logo {
        display: inline-block;
        width: 110px;
        height: 28px;
        margin: 10px 54px 0 0;
    }

    &__logo-img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }

    &__list {
        display: flex;
        // gap: 0 24px;
    }

    &__list-item {
        &:nth-child(2),
        &:nth-child(5) {
            &:hover {
                & > .dropdown__content {
                    opacity: 1;
                }
            }
        }
        z-index: 1;
        &:hover {
            z-index: 4;
        }
        span {
            position: relative;
        }
    }

    &__list-link {
        padding: 0 15px;
        color: #fff;
        line-height: 54px;
        display: inline-block;
        font-size: 14px;
        font-weight: 700;
        position: relative;
        z-index: 1;

        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            background-color: transparent;
            width: 100%;
            height: 100%;
            z-index: -1;
        }
        &:hover {
            &::before {
                background-color: rgba(255, 255, 255, 0.1);
            }
        }
    }
    @media screen and (max-width: 1024px) {
        &__logo {
            // display: inline-block;
            // width: 110px;
            // height: 28px;
            margin: 10px 10px 0 0;
        }
    }
    @media screen and (max-width: 768px) {
        height: 54px;
        &__burger {
            display: inline-block;
            margin-right: auto;
        }
        &__logo {
            margin: 10px auto 0 -24px;
        }
        &__list {
            display: none;
        }
    }
}
.dropdown__btn {
    position: relative;
    padding: 0 35px 0 15px;
    &::after {
        content: "";
        position: absolute;
        right: 10px;
        top: calc(50%);
        background-image: url(../images//arrow.svg);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        width: 19px;
        height: 19px;
        transform: translateY(-50%);
    }
}
.dropdown__content {
    transition: 0.3s all;
    opacity: 0;

    z-index: 0;
    background-color: #5c42aa;
    position: absolute;
    max-width: 175px;
    width: 100%;
    // z-index: 2;
    // bottom: -54px;
    &-item {
    }
    &-link {
        display: block;
        padding: 10px 15px;
        font-size: 14px;
        line-height: 20px;
        color: #fff;
        font-weight: 700;
        position: relative;
        z-index: 1;

        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            background-color: transparent;
            width: 100%;
            height: 100%;
            z-index: -1;
        }
        &:hover {
            z-index: 4;
            &::before {
                background-color: rgba(255, 255, 255, 0.1);
            }
        }
    }
}

.menu {
    transform: translateX(-100%);
    transition: transform 0.5s cubic-bezier(0.79, 0.14, 0.15, 0.86);
    &.active {
        transform: translateX(0);
    }
    position: fixed;
    z-index: 10000000;
    background-color: rgba($color: #313b45, $alpha: 0.98);
    max-width: 100vw;
    width: 100%;
    min-height: 100vh;
    height: auto;
    padding-left: 20px;
    padding-right: 20px;
    &__socials {
        margin-top: 40px;
        display: flex;
        gap: 0 20px;
        // padding-left: 20px;
    }

    &__socials-item {
        &:nth-child(1) {
            margin-top: 5px;
            svg {
                width: 22px;
                height: 22px;
            }
        }

        &:nth-child(2) {
            margin-top: 5px;
            svg {
                width: 22px;
                height: 22px;
                path {
                    stroke: rgba($color: #313b45, $alpha: 0.98);
                    &:nth-child(1) {
                        stroke: none;
                    }
                }
            }
        }
        &:nth-child(3) {
            margin-top: 4px;
            svg {
                width: 23px;
                height: 23px;
            }
        }
        &:nth-child(4) {
            margin-top: 3px;
            svg {
                width: 26px;
                height: 26px;
            }
        }
        &:nth-child(5) {
            svg {
                width: 30px;
                height: 30px;
            }
        }
    }

    &__socials-link {
        display: inline-block;
        height: 50px;
        svg {
            width: 100px;
            height: 100px;

            path {
                fill: #fff;
            }
            rect {
                stroke: transparent;
            }
        }
    }

    &__list {
    }

    &__list-item {
        button,
        a {
            display: block !important;
        }
        button {
            position: relative;
            width: 100%;
            text-align: left;
        }
        &:nth-child(2),
        &:nth-child(5) {
            &:hover {
                ul {
                    max-height: 180px;
                }
                button {
                    &::after {
                        transform: translateY(-50%) rotate(0deg);
                    }
                }
            }
            button {
                &::after {
                    transition: 0.3s all;

                    content: "";
                    position: absolute;
                    right: 15px;
                    top: 50%;
                    transform: translateY(-50%) rotate(-90deg);
                    background-image: url(../images/arrow.svg);
                    background-repeat: no-repeat;
                    background-position: center;
                    background-size: cover;
                    width: 30px;
                    height: 30px;
                }
            }
        }
    }

    &__list-link,
    &__dropdown-link {
        display: inline-block;
        line-height: 21px;
        font-size: 15px;
        font-weight: 500;
        color: #fff;
        margin-left: 0;
        padding: 15px 30px 15px 0;
    }
    &__dropdown-link {
        padding: 9px 30px;
        line-height: normal;
    }
    &__dropdown-btn {
    }

    &__dropdown {
        max-height: 0;
        overflow: hidden;
        transition: max-height 0.4s ease-out;
    }

    &__dropdown-item {
    }
    &__close {
        display: inline-block;
        position: absolute;
        right: 5%;
        top: 20px;
        width: 70px;
        height: 70px;
        // rotate: 45deg;
        span {
            position: absolute;
            width: 40px;
            height: 2px;
            background-color: #fff;
            left: 50%;
            top: 50%;

            &:first-child {
                transform: translate(-50%, -50%) rotate(45deg);
            }
            &:last-child {
                transform: translate(-50%, -50%) rotate(-45deg);
            }
        }
    }
}
.promo {
    position: relative;
    z-index: 4;
    .promo__wrapper {
        position: absolute;
        &._baribar {
            top: 32px;
            z-index: 2;
            max-width: 1515px;
            width: 100%;
            left: 50%;
            transform: translateX(-50%);
        }
        &._woman {
            right: 8.4%;
            z-index: 3;
            &::before {
                content: "";
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 30%;
                background-color: rgba(0, 0, 0, 0.2); /* Полупрозрачный черный цвет */
                // backdrop-filter: blur(10px); /* Размытие */
            }
        }
        &._circle {
            top: 32px;
            right: 8.4%;
            z-index: 1;
            &::after {
                content: "";
                position: absolute;
                max-width: 685px;
                max-height: 833px;
                left: -5%;
                width: 100%;
                height: 100%;
                background-image: url(../images/shadow.svg);
                background-size: cover;
                background-repeat: no-repeat;
                z-index: 4;
            }
        }
    }
    img {
        // position: absolute;
    }

    &__title {
        position: relative;
        z-index: 5;
        font-size: 128px;

        font-weight: 700;
        color: #fff;
    }
}
.footer {
