        display: inline-flex;
        justify-content: flex-end;
        align-items: center;
        gap: 0 36px;
    }

    &__social-item {
    }

    &__social-link {
    }

    &__text {
        line-height: 155.187%;
        color: rgba(255, 255, 255, 0.63);
        &:first-of-type {
            margin: 30px 0 10px 0;
            max-width: 616px;
            width: 100%;
        }
        &:last-of-type {
            color: #fff;
            width: 100%;
            margin-bottom: 10px;
        }
