    }

    &__copyright {
        color: rgba(255, 255, 255, 0.29);
    }

    &__list {
        display: flex;

        gap: 0 30px;
    }

    &__list-item {
    }

    &__list-link {
        color: rgba(255, 255, 255, 0.63);
    }
    @media screen and (max-width: 768px) {
        font-size: 12px;
        &__logo {
            width: 67px;
            height: 12px;
        }
        &__text {
            color: rgba(255, 255, 255, 0.63);
            &:first-of-type {
                margin: 25px 0;
            }
            &:last-of-type {
                margin-bottom: 0;
            }
        }
        &__social {
            order: 3;
            width: 100%;
            justify-content: space-between;
            margin: 25px 0;
        }

        &__list {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            grid-template-rows: repeat(2, 1fr);
            grid-column-gap: 0px;
            grid-row-gap: 0px;
            gap: 0 33px;
            &-link {
                line-height: 155.187%;
            }
        }
        &__bottom {
            order: 4;
            flex-wrap: wrap-reverse;
            justify-content: center;
        }
        &__copyright {
            margin-top: 35px;
        }
    }
}

