.news {
    margin-bottom: 54px;

    &__container {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(5, auto);
        // grid-column-gap: 0px;
        // grid-row-gap: 0px;
        gap: 21px;
        align-content: end;
        @media screen and (max-width: 500px) {
            display: flex;
