html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

html,
body {
    // height: 100%;
}
main {
    z-index: 3;
}
body {
    position: relative;
    font-family: "Roboto", sans-serif;
    z-index: 1;
    // overflow: auto;
    &.active {
        overflow: hidden;
        // margin-right: 16px;
    }
    &._qorqa {
        background: #e9e4e0;
        @media screen and (max-width: 768px) {
            background: #000;
        }
    }
    &._choco {
        // background-image: url(../images/bg-choco.webp) !important;
        // background-repeat: no-repeat;
    }
    .background {
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: -1;
        object-fit: cover;
        @media screen and (max-width: 1024px) {
            display: none;
        }
    }
    // &._choco {
    //     background-image: url(../images/bg-choco.jpg) !important;
    //     // width: 100vw;
    //   width: auto;
    //   height: auto;
    //     // height: 100%;
    //     background-size: cover;
    //     background-position: center;
    // }
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    display: none;
}
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}

ol,
ul {
    list-style: none;
}
blockquote,
q {
    quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
    content: "";
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
a {
    text-decoration: none;
    color: inherit;
}
address {
    font-style: inherit;
}
p {
    margin: 0;
}

ul,
ol,
li {
    margin: 0;
    padding: 0;
    list-style: none;
}

button {
    display: block;
    background: none;
    border: none;
    padding: 0;
    cursor: pointer;
}
input {
    border: inherit;
    padding: 0;
    margin: 0;
}
.wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100%;
}

.content {
    flex: 1 0 auto;
}

.footer {
    flex: 0 0 auto;
}

.container {
    max-width: 1070px;
    width: 100%;
    padding: 0 15px 0;
    margin: 0 auto;
    @media screen and (max-width: 499px) {
        padding: 0 45px 0;
    }
}
.button-more {
    // display: inline-flex;
    // align-items: center;
    // justify-content: center;
    color: #fff;
    font-size: 18px;
    font-weight: 700;
    line-height: 131%;
    margin: 0 auto 40px;
    max-width: 207px;
    padding: 10px 40px;
    width: 100%;
    background-color: #9572ff;
    border-radius: 40px;
    transition: 0.3s all;
    &:hover {
        background: #7957dd;
    }
    &:active{
        background: #441262;
    }
    // &:focus {
    //     background: #652d86;
    // }
    @media screen and (max-width: 499px) {
        max-width: 100%;
    }
}
.visually-hidden {
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    height: 1px;
    overflow: hidden;
    position: absolute;
    white-space: nowrap;
    width: 1px;
}
.reset-button {
    margin: 0;
    border: 0;
    border: 1px solid black;
    padding: 0;
    font-family: inherit;
    text-align: left;
    // font-size: 1em;
    color: inherit;
    background-color: transparent;
    outline: none;
    cursor: pointer;
    &:disabled {
        cursor: default;
    }
}

